import { Component, OnInit } from '@angular/core';
import { ModalForm } from 'src/app/models/modal-form';
import { ModalFormInterface } from 'src/app/interfaces/modal-form-interface';
import { NavParams, ModalController } from '@ionic/angular';
import { PlatformLocation } from '@angular/common';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { SearchPage } from '../../search/search.page';

@Component({
  selector: 'app-form-balance-sheet',
  templateUrl: './form-balance-sheet.page.html',
  styleUrls: ['./form-balance-sheet.page.scss'],
})
export class FormBalanceSheetPage extends ModalForm implements OnInit, ModalFormInterface {
 
  public team_id ;
  public balanceSheet : any;
  public modelBalanceSheet: Model;
  public amountOfTheMonth : 0;


  constructor(
    public navParms : NavParams,
    public modalController : ModalController,
    location : PlatformLocation,
    private requestService : RequestService,
    private dialogService : DialogService
  ) {
    super(location,modalController)

    this.team_id = navParms.get('team_id');
    this.amountOfTheMonth = navParms.get('amount_of_the_month');

    this.modelBalanceSheet = new Model('BalanceSheet',requestService);
   }


  initObject() {

    this.balanceSheet = {
      user : "",
      user_id : '',
      balance : 0,
      team_id : this.team_id,
      balance_start:0

    }

    //this.addUser();
  }




  async submit(){

    await this.dialogService.loadingProgres();

    console.log('submit');
    this.modelBalanceSheet.api_create(this.balanceSheet).subscribe((respose) => {
      this.dialogService.dimissLoaging();
      if(respose['status'] == 'success'){
        
        this.dimiss(respose['BalanceSheet']);
      }
    },
    error => {
      this.dialogService.dimissLoaging();
    },()=>{
      this.dialogService.dimissLoaging();
    })
  }


  dimiss(balanceSheet){
    this.modalController.dismiss({
      balanceSheet : balanceSheet
    })
  }

  close() {
    this.modalController.dismiss();
  }

  log(event){
    console.log(event)
  }

  async addUser(e = null){
    //this.errorAdmins = '';
    if(e)
      e.preventDefault();
    
      console.log('Add admins');


  

    const modal = await this.modalController.create({
      component: SearchPage ,
      componentProps: { 
        cancellable: false,
        model : 'User',
        function : 'searchUser',
        multiple : false,
        textSearch : 'Nombre o email',
        listSelect : []
      }
    });

    modal.onDidDismiss().then(data=>{
      if(data.data && data.data.item){
        const user = data.data['item'];
      //console.log('users_select' , list);
        this.balanceSheet.user = user.text;
        this.balanceSheet.user_id = user.id;
      }
    })

    return await modal.present();
  }

}
