import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { ModalController, ActionSheetController } from '@ionic/angular';
import { FormBalanceSheetPage } from './form-balance-sheet/form-balance-sheet.page';
import { UtilArrayService } from 'src/app/services/util-array.service';

@Component({
  selector: 'app-balance-sheet',
  templateUrl: './balance-sheet.page.html',
  styleUrls: ['./balance-sheet.page.scss'],
})
export class BalanceSheetPage implements OnInit {

  public team_id ;
  public modelBalanceSheet : Model;
  public modelTeam : Model;
  public modelAmountTeam : Model;

  public team : any;

  public filter ='';

  constructor(
    public route : ActivatedRoute,
    private router : Router,
    private requestService : RequestService,
    public dialogService : DialogService,
    public authUser : AuthUserService,
    private modalController : ModalController,
    public utilArray : UtilArrayService,
    public actionSheetController: ActionSheetController
  ) { 
    
    this.modelBalanceSheet =  new Model('BalanceSheet',requestService)
    this.modelTeam = new Model('Team', requestService);
    this.modelAmountTeam = new Model('AmountTeam',requestService);

    this.team_id = this.route.snapshot.paramMap.get('team_id');
    this.filter = this.route.snapshot.paramMap.get('filter');

  }

  ngOnInit() {
      this.init();
    
  }

  async init(){
    await this.dialogService.loadingProgres();

    let param = {};
    if(this.route.snapshot.queryParamMap.get('pending'))
      param['pending'] = 1

    this.modelTeam.api_functionModel(this.team_id,'pageBalanceSheet',param).subscribe(
      response => {
        //console.log('response Team',response);
        if(response['status'] == 'success'){
          this.team = response['data'];
        }
        this.dialogService.dimissLoaging();
      },
      error => {
        this.dialogService.dimissLoaging()
      }
    );
    
  }

  async updateAmount(){
    let newAmont = prompt('Ingrese el nuevo valor',this.team.amount_balance);

    if(newAmont){
      await this.dialogService.loadingProgres('Modificando moto');

      this.modelAmountTeam.api_create({price : newAmont,team_id : this.team_id}).subscribe(
        response => {
          if(response['status'] == 'success'){
            this.team.amount_balance = newAmont;
          }
          this.dialogService.dimissLoaging();
        },
        error => {
          this.dialogService.dimissLoaging();
        }
      )
    }
  }


  async add(){
    const modal = await this.modalController.create({
      component: FormBalanceSheetPage ,
      componentProps : {
        team_id : this.team_id,
        amount_of_the_month : this.team.amount_balance
      }
    });

    modal.onDidDismiss().then(data=>{
      if(data.data && data.data['balanceSheet']){
        const balanceSheet = data.data['balanceSheet'];
        //this.playerService.listAddLast(player);
        this.utilArray.listAddFirst(this.team.balance_sheets , balanceSheet);
      }
    })

    return await modal.present();
  }


  async showOptions(balanceSheet){

    let buttons = [];

    buttons.push( {
      text: 'Pagar',
      icon: 'cash',
      handler: () => {
        console.log('Cancel clicked');
        this.paid(balanceSheet);
      }
    });

    buttons.push( {
      text: 'Ver meses',
      icon: 'calendar',
      handler: () => {
        console.log('Ver meses clicked');
        this.router.navigateByUrl('/balance-sheet/months/' + balanceSheet.id);
      }
    });

    buttons.push( {
      text: 'Ver Pagos',
      icon: 'wallet',
      handler: () => {
        console.log('Ver meses clicked');
        this.router.navigateByUrl('/balance-sheet/payments/' + balanceSheet.id);
      }
    });

    /*
    buttons.push( {
      text: 'Modificar',
      icon: 'create-outline',
      handler: () => {
        console.log('Edit clicked');
      }
    })
*/
    buttons.push({
      text: 'Eliminar',
      role: 'destructive',
      icon: 'trash',
      handler: () => {
        this.deleteBalanceSheet(balanceSheet);
        console.log('Delete clicked');
      }
    })
    
    
    buttons.push( {
      text: 'Cancelar',
      icon: 'close',
      role: 'cancel',
      handler: () => {
        console.log('Cancel clicked');
      }
    });

    const actionSheet = await this.actionSheetController.create({
      header: 'Operaciones de : ' + balanceSheet.user.completeName,
      cssClass: 'my-custom-class',
      buttons: buttons
    });
    await actionSheet.present();
  }

  /**
   * Elimina la Hoja de balance de un usuario
   */
  async deleteBalanceSheet(balanceSheet){
    await this.dialogService.loadingProgres('Eliminando..');

    this.modelBalanceSheet.api_delete(balanceSheet.id).subscribe(
      response => {
        this.dialogService.dimissLoaging();
        this.utilArray.listDelete(this.team.balance_sheets,balanceSheet.id);
      },
      error => {
        this.dialogService.dimissLoaging();
        //this.dialogService.showToast('Error al eliminar Registro, tiene registos asociados');
      }
    )
  }

  async paid( balanceSheet){
    let value = prompt('Monto a pagar','0');

    if(parseInt( value ) > 0 ){

      if(confirm('Estas seguro de realizar el pago de ' +  value + ' para ' + balanceSheet.user.completeName)){

        await this.dialogService.loadingProgres('registrando pago...');

        this.modelBalanceSheet.api_functionModel(balanceSheet.id,'addPayment',{amount : value}).subscribe(
          response => {
            this.dialogService.dimissLoaging();
            if(response['status'] == 'success' ){
              this.utilArray.listUpdate(this.team.balance_sheets,balanceSheet.id , response['data']);
            }

            console.log('response addPayment' , response);
          },
          error => {
            this.dialogService.dimissLoaging();
          }
        );


      }
    }
  }


}
