import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { BalanceSheetPage } from './balance-sheet.page';

const routes: Routes = [
  {
    path: '',
    component: BalanceSheetPage
  },
  {
    path: 'form-balance-sheet',
    loadChildren: () => import('./form-balance-sheet/form-balance-sheet.module').then( m => m.FormBalanceSheetPageModule)
  }
  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class BalanceSheetPageRoutingModule {}
